<template>
	<div class="filter" v-if="articles.length">
		<article-cover
			class="article-list-item"
			v-for="(article, i) in articles"
			:info="article"
			:key="i"
			v-once
		/>
	</div>
</template>

<script>
	
	import ArticleCover from '@/components/ArticleCover/ArticleCover'
	
	import get from '@/network/get'
	import { toGMT } from '@/assets/script/lib' 
	
	export default {
		name: 'FilterPage',
		data() {
			return {
				articles: []
			}
		},
		components: {
			ArticleCover
		},
		created() {
			const url = `/${this.$route.query.name.toLowerCase()}`
			const query = { [this.$route.query.name.toLowerCase()]: this.$route.query.filter }
			
			get(url, query).then(data => {
				data.forEach(art => art.date = toGMT(art.date))
				this.articles = data
			})
		}
	}
	
</script>

<style lang="stylus" scoped>
	
	@import '~assets/stylus/variable.styl'
	
	.filter
		width 100%
	
		.article-list-item
			height 100vh
			margin-bottom 8rem
			
			& >>> .article-cover-mask
				left 30%
				
			@media screen and (max-width: 400px)
				& >>> .article-cover-mask
					left 0
				
		.article-list-item:last-child
			margin-bottom 0
	
</style>
